import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// 时间处理
import moment from 'moment';
Vue.prototype.$moment = moment
moment.locale('zh-cn')
Vue.filter('time', function(value, arg){
  return moment(value).format(arg);
})
// End

// 代码高亮
import Hljs from 'highlight.js';
import 'highlight.js/styles/mono-blue.css'; // 代码高亮风格，选择更多风格需导入 node_modules/hightlight.js/styles/ 目录下其它css文件

import '@/page/articleDetail/style/post.scss';
Vue.directive('highlight',function (el) {
  let blocks = el.querySelectorAll('pre code');
  blocks.forEach((block)=>{
    Hljs.highlightBlock(block)
  })
})
// End


import MyConfig from '@/config';
Vue.prototype.$config = MyConfig;


//  组件
import './utils/global'
import Auth from '@/utils/auth'
// import Message from '@/utils/message'
Vue.prototype.$auth = Auth;
// Vue.prototype.$msg = Message;

//  viewer
import 'viewerjs/dist/viewer.css'

// Icon
import './assets/font/iconfont.js';

// 样式
import 'normalize.css/normalize.css'  // 清除样式
import './assets/styles/global.scss';
import '@/utils/print';



Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
