<template>
    <div class="tl-hamburger" ref="menuLayer">
        <div class='menu-secondary-background-color' ref="reveal1"></div>
        <div class="tl-menu-overlay" ref="reveal2">
            <div class="tl-f-nav-list">
                <div class="menu-background"></div>
                <div class="container">
                    <div class="tl-f-nav-col">
                        <h3 class="tl-f-nav-item"
                            @click="isOpenOverlay = !isOpenOverlay"
                            @mouseenter="handleHover($event)"
                            @mouseleave="handleHoverExit($event)"
                            v-for="(nav,index) in navList"
                            :key="index"
                        >
                            <router-link :to="nav.path">
                               <div class="inner" ref="links">
                                   <!--Content-->
                                   <div class="tl-menu-content" >
                                       <div class="tl-menu-index">
                                           0{{index+1}}
                                       </div>
                                       <div class="tl-menu-name">
                                           {{nav.name}}
                                       </div>
                                   </div>
                                   <!--Icon-->
                                   <div class="tl-menu-icon">
                                       <div class="tl-menu-icon_typeSVG">
                                           <svg xmlns="http://www.w3.org/2000/svg" width="17" height="17" viewBox="0 0 17 17"> <g fill="none" fill-rule="evenodd" stroke="#000" stroke-linecap="round" stroke-linejoin="round" transform="rotate(-135 7.533 9.872)"> <path d="M7.80044955,21.4512363 L7.81236952,0.231533347" style="stroke-dashoffset: 0; stroke-dasharray: 22.2197px, 32.2197px;"></path> <path d="M15.601 13.651 7.8 21.451 0 13.651" style="stroke-dashoffset: 0; stroke-dasharray: none;"></path> </g> </svg>
                                       </div>
                                   </div>
                                   <!--Border-->
                                   <div class="tl-menu-border"></div>
                               </div>
                            </router-link>
                        </h3>

                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import gsap from 'gsap';
import {
    staggerReveal,
    staggerRevealClose,
    staggerText
} from './js/anime'
export default {
    name: "Humburger",
    data(){
        return {
            navList:[
                {
                    name: '博客',
                    path: '/blog',
                },
                {
                    name: '闲记',
                    path: '/life',
                },
                {
                    name: '关于',
                    path: '/blog/post/33'
                },
                {
                    name: '工具',
                    path: '/tools/'
                }
            ]
        }
    },
    methods:{
        handleHover (e){
            gsap.to(e.target, {
                duration: 0.3,
                y: 3,
                skewX: 4,
                ease: "power1.inOut"
            });
        },
        handleHoverExit (e){
            gsap.to(e.target, {
                duration:0.6,
                y: -3,
                skewX: 0,
                ease: "power1.inOut"
            });
        }
    },
    watch: {
        isOpenOverlay(){
            let menuLayer = this.$refs.menuLayer;
            let reveal1 = this.$refs.reveal1;
            let reveal2 = this.$refs.reveal2;
            let links = this.$refs.links;

            let btn = document.querySelector(".menu-btn");
            let closeText = document.querySelector(".tl-menu-trigger");

            if(this.isOpenOverlay==false){
                btn.classList.toggle("active");closeText.classList.toggle("open");
                staggerRevealClose(reveal2, reveal1);
                gsap.to(document.querySelector(".tl-menu-overlay .container"),
                    { duration: 0, css: { display: "none" } });
                gsap.to(menuLayer, { duration: 1, css: { display: "none" } });
            }else{
                btn.classList.toggle("active");closeText.classList.toggle("open");
                // 设置可见
                gsap.to(menuLayer, { duration: 0, css: { display: "block" } });
                // 逐个出现
                gsap.to([reveal1, reveal2], {
                    duration: 0,
                    opacity: 1,
                    height: "100%"
                });
                gsap.to(document.querySelector(".tl-menu-overlay .container"),
                    { duration: 0, css: { display: "flex" } });
                staggerReveal(reveal1, reveal2);

                staggerText(document.querySelectorAll(".tl-f-nav-item .inner "));
            }
        }
    },
    props:['isOpenOverlay']
}
</script>

