            a{
                text-decoration: none;
            }
            &:hover .tl-menu-icon{ //Hover Icon偏移
                transform: rotate(45deg);
            }

            &:hover .tl-menu-border:after{
                transform-origin: right top;
                transform: scaleX(1);
            }
            .inner{
                width:100%;
                display: flex;
                justify-content: space-between !important;
                align-items: center;
                position: relative;
                font-weight: 400;
                padding-bottom: 25px;
                .tl-menu-content{
                    display: flex;
                    align-items: flex-start;
                    .tl-menu-index{
                        font-size:11px;
                        color:#201d1e;
                    }
                    .tl-menu-name{
                        font-size: 28px;
                        color:#201d1e;
                        margin-left: 36px;
                        font-weight: bold;
                    }
                }
            }


            .tl-menu-icon{
                transition:all 0.5s;
                transform-origin:center center;
                svg{
                    width:17px;
                    height:17px;
                }
            }
            // 底部边框
            .tl-menu-border{
                position:absolute;
                bottom:0;
                left:0;
                display: block;
                width:100%;
                height:1px;
                background-color:#201d1e;
                transform: scaleY(0.5);
                transition:all 0.3s;
                transform-origin: left bottom;
                &:after{
                    content:"";
                    display: block;
                    position: absolute;
                    top:0;
                    left:0;
                    width:100%;
                    height:2px;
                    background: #000;
                    transform: scaleX(0) ;
                    transition:all 0.7s;
                    transform-origin: left top !important;
                }
            }
        }

    }


@media (max-width: 500px) {
    .tl-hamburger{
        .tl-menu-overlay{
            .container{
                width:100%;
            }
        }
    }
}
