import gsap from 'gsap';

export const pageTitime = ()=>{
    let tl = gsap.timeline();
    tl.from(".tl-page-type-title",{
      duration:1,
      skewX:-45,
      opacity:0
    });
    tl.to(".tl-page-title h1",{ // Type Title
      duration:0.6,
      x:0,
    })
  };
  
  
  
  
  // 打开按钮
  export const staggerReveal = (node1, node2) => {
    gsap.from([node1, node2], {
      duration: 0.8,
      height: 0,
      transformOrigin: "right top",
      skewY: 2,
      ease: "power3.inOut",
      stagger: {
        amount: 0.2
      }
    });
  };
  
  // 关闭按钮
  export const staggerRevealClose = (node1, node2) => {
    gsap.to([node1, node2], {
      duration: 0.8,
      height:0,
      ease: "power3.inOut",
      stagger: {
        amount: 0.2
      },
    });
  };
  
  // STAGGER THE LINKS TO APPEAR
  export const staggerText = (node) => {
    gsap.from(node, {
      duration: 1.8,
      y: 100,
      delay: 0.5,
      skewY: 2,
      opacity:0,
      ease: "power3.inOut",
      stagger: {
        amount: 0.3
      }
    });
  };
  
  // Fade up for the additonal info on our menu
  export const fadeInUp = node => {
    gsap.from(node, {
      y: 60,
      duration: 1,
      delay: 0.2,
      opacity: 0,
      ease: "power3.inOut"
    });
  };
  
  // Hover on the link
  export const handleHover = e => {
    gsap.to(e.target, {
      duration: 0.3,
      y: 3,
      skewX: 4,
      ease: "power1.inOut"
    });
  };
  
  // Hover off the link
  export const handleHoverExit = e => {
    gsap.to(e.target, {
      duration: 1.3,
      y: -3,
      skewX: 0,
      ease: "power1.inOut"
    });
  };
  
  // adds city image once you hover on
  export const handleCity = (city, target) => {
    gsap.to(target, {
      duration: 0,
      background: `url(${city}) center center`
    });
    gsap.to(target, {
      duration: 0.4,
      opacity: 1,
      ease: "power3.inOut"
    });
    gsap.from(target, {
      duration: 0.4,
      skewY: 2,
      transformOrigin: "right top"
    });
  };
  
  // Removes the city image once you hover off
  export const handleCityReturn = target => {
    gsap.to(target, {
      duration: 0,
      skewY: 0
    });
    gsap.to(target, {
      duration: 0.4,
      opacity: 0,
      skewY: 0
    });
  };
  