<script>
import Humburger from './Humberger.vue';
import gsap from 'gsap';
export default {
    components: {
      Humburger
    },
    props: [
      'navList',
    ],
    data(){
        return {
            isOpenOverlay:false, // 控制汉堡菜单
        }
    },
    methods: {
        handleOverlay(){
            console.log("Click Menu");
            this.isOpenOverlay = !this.isOpenOverlay;
        }
    },
    mounted() {
      window.addEventListener('scroll', ()=>{
        let top = document.documentElement.scrollTop;
        if(top===0){
          document.querySelector('.tl-logo h1 a').style.opacity = 1;
          gsap.to('.tl-logo', {
            duration: 1,
            scale: 1,
          })
        }else{
          document.querySelector('.tl-logo h1 a').style.opacity = 0;
          gsap.to('.tl-logo', {
            duration: 1,
            scale: 0.5
          })
        }
      })
    }
}
</script>

