<template>
    <div class="music-tools">
        <div id="aplayer" class="hidden-xs"></div>
    </div>
</template>

<script>
    import axios from 'axios';
    import 'aplayer/dist/APlayer.min.css';
    import APlayer from 'aplayer'
    export default {
        name: "index",
        data(){
            return {
                videoList: [],
            }
        },
        methods: {
            // 加载音乐
            loadData(){
                const that = this;
                let url = 'http://123.57.160.114:3000/playlist/detail?id=5318266731';
                axios.get(url).then(res=>{
                    if(res.data.code === 200){
                        res.data.playlist.tracks.forEach((item)=>{
                            let id = item.id;
                            let name = item.name; // 音频名称
                            let artist = item.ar[0].name; // 音频艺术家
                            let url = `https://music.163.com/song/media/outer/url?id=${id}.mp3`; // 音频链接
                            let cover = item.al.picUrl; // 音频封面
                            let obj = {
                                name,
                                artist,
                                url,
                                cover,
                                lrc,
                            };
                            var lrc = '';
                            axios.get(`http://123.57.160.114:3000/lyric?id=${id}`).then(res=>{
                                if(res.data.lrc){
                                    lrc = res.data.lrc.lyric
                                    obj.lrc = lrc;
                                }
                            
                            })
                            setTimeout(()=>{
                                that.videoList.push(obj)
                            }, 1000)
                        });
                        setTimeout(()=>{
                            this.$nextTick(()=>{
                                const ap = new APlayer({
                                    container: document.getElementById('aplayer'),
                                    lrcType: 1,
                                    listMaxHeight: 90,
                                    fixed: true,
                                    mini: true,
                                    theme: '#a98b98',
                                    audio: this.videoList,
                                });
                                // ap.lrc.show();
                                ap.lrc.hide();
                            })
                        }, 1000)
                    }
                })
            }
        },
        mounted() {
            this.loadData();
        }
    }
</script>

<style scoped lang="scss">
.music-tools{
    position: absolute;
    bottom: 0px;
    left: 0px;
    /*background: blue;*/

}
</style>