<template>
  <div id="app">
    <THeader  :navList=navList />
    <Music/>
    <router-view/>
    <div v-if="!isShow">
        <LoadingPage/>
    </div>
  </div>
</template>

<script>
import THeader from './components/THeader/index';
import LoadingPage from './components/LoadingPage';
import Music from './components/music';
import {
  getNav
} from '@/api/blog';
import { mapState } from 'vuex';
export default {
  components: {
    THeader,
    LoadingPage,
    Music: Music
  },
  computed: {
    ...mapState(['isShow'])
  },
  data(){
    return {
      navList: [],
    }
  },
  methods: {
    loadNav(){
      getNav().then(res=>{
        if(res.code === 200){
          this.navList = res.data;
        }
      })
    }
  },
  mounted(){
    this.loadNav();
  }
}
</script>
<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
</style>
