
import request from '@/utils/request.js';


/**
 * 查询导航
 */
export function getNav(){
    return request({
        url: '/blog/navbar/',
        method: 'get',
    })
}

// 查询文章
/**
 * category 分类
 */
export function getAll(data){
    return request({
        url: `/blog/post/`,
        method: 'get',
        params: data,
    })
}
export function postTop(){
    return request({
        url: '/blog/post/topic/',
        method: 'get',
    })
}

// 查询所有分类信息
export function getCategory(id){
    let url = '';
    if(id){
        url = `/blog/category/${id}`
    }else{
        url = `/blog/category/`
    }
    return request({
        url: url,
        method: 'get',
    })
}
// 查询所有标签
export function getTags(){
    return request({
        url: '/blog/tag/',
        method: 'get'
    })
}

export function getTimeline(){
    return request({
        url: '/blog/timeline/',
        method: 'get'
    })
}